<!-- by 添少 -->
<template>
  <fixed-layout>
    <template slot="header">
      <topbar>
        <template slot="before">
          <b class="iconfont icon-back" @click="$router.go(-1)"></b>
        </template>
        {{title}}详情
      </topbar>
    </template>
    <section class="m-scroll" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading"
             infinite-scroll-distance="10">
      <router-view></router-view>
      <section class="detail">
        <tab :select.sync="tab.select" :data="tabList" @change="handChangeTab"></tab>
        <section class="tc1" :style="{ 'min-height' :minHeight}">
          <template v-if="list.length">
            <template v-if="tab.select===0||tab.select===1||tab.select===2">
              <rendezvous-list :Lists="list"></rendezvous-list>
              <template v-if="isNone">
                <p style="text-align: center;padding: 10px;">没有更多了</p>
              </template>
              <!--<section class="ui-thumb">-->
              <!--<div class="cover">-->
              <!--<img class="db fit-cover" :src="item.preimg"/>-->
              <!--</div>-->
              <!--<div class="text">-->
              <!--<h4 class="l-e l1">{{item.title}}</h4>-->
              <!--<p class="info">Sasa俱乐部<b>俱乐部</b></p>-->
              <!--<p class="l-e l2">{{item.desc}}</p>-->
              <!--</div>-->
              <!--</section>-->
            </template>
            <template v-else-if="tab.select===3">
              <forum-list :forumLists="list"></forum-list>
            </template>
          </template>
          <template v-else>
            <p style="text-align: center;padding: 10px;">暂无内容</p>
          </template>
        </section>
      </section>
    </section>
  </fixed-layout>
</template>

<script>
  import {mapState} from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import Tab from '@/components/tab'
  import RendezvousList from '@/views/rendezvous/rendezvousList'
  import ForumList from '@/views/detail/components/forumList'

  export default {
    name: 'coach',
    components: {
      FixedLayout,
      Topbar,
      Tab,
      RendezvousList,
      ForumList
    },
    data() {
      return {
        tab: {
          select: 0,
          list: [
            {value: 5, label: '课程'},
            {value: 3, label: '行程'},
            {value: 4, label: '装备'},
            {value: 2, label: '帖子'},
          ]
        },
        list: [],
        lastProductId: 0,
        isNone: false,
      }
    },
    watch: {
      id() {
        console.log(0)
        this.getList(this.tab.select === 3 && 1)
      }
    },
    computed: {
      ...mapState({
        title: state => state.detail.title,
        id: state => state.detail.id,
      }),
      minHeight() {
        return (this.list.length == 0 ? '13.34rem' : '0');
      },
      tabList() {
        let list = []
        this.tab.list.forEach(e => list.push(e.label))
        return list
      }
    },
    created() {
      // window.$v = this
    },
    methods: {
      init() {
      },
      handChangeTab(index) {
        // console.log(index)
        this.list = []
        this.lastProductId = 0
        this.isNone = false
        this.getList(this.tab.select === 3 && 1)
      },
      getList(type) {
        if (type === 1) {
          // 帖子
          this.$http.get(this.$port.GET_DISCUSS_LIST, {
            params: {
              userId: this.id,
              lastDiscussId: this.lastProductId
            }
          }).then(res => {
            if (res.code === 200) {
              if (res.data.length && res.data[res.data.length - 1].discussId !== this.lastProductId) {
                this.list = this.list.concat(res.data)
                this.lastProductId = res.data[res.data.length - 1].discussId
                this.isNone = false
              } else {
                this.isNone = true
              }
            }
          })
        } else {
          let params = {
            userId: this.id,
            lastProductId: this.lastProductId
          }
          params.contType = this.tab.list[this.tab.select].value
          this.$http.get(this.$port.GET_PRODUCT_LIST, {
            params
          }).then(res => {
            if (res.code === 200) {
              if (res.data.length && res.data[res.data.length - 1].productId !== this.lastProductId) {
                this.list = this.list.concat(res.data)
                this.lastProductId = res.data[res.data.length - 1].productId
                this.isNone = false
              } else {
                this.isNone = true
              }
            }
          })
        }
      },
      loadMore() {
        this.getList(this.tab.select === 3 && 1)
      }
    }
  }
</script>

<style lang="scss" scoped>
  /*.ui-thumb {*/
  /*font-size: 14px;*/
  /*display: flex;*/
  /*.cover {*/
  /*height: 100px;*/
  /*flex: 0 0 130px;*/
  /*}*/
  /*.text {*/
  /*text-align: justify;*/
  /*padding-left: 10px;*/
  /*h4 {*/
  /*font-size: 18px;*/
  /*}*/
  /*.info {*/
  /*color: #888;*/
  /*b {*/
  /*background-color: #d33c44;*/
  /*font-size: 11px;*/
  /*color: #fff;*/
  /*padding: 0 5px;*/
  /*margin-left: 0.5em;*/
  /*display: inline-block;*/
  /*border-radius: 5px;*/
  /*}*/
  /*}*/
  /*flex: 1;*/
  /*}*/
  /*}*/

  .layout {
    background-color: #fff;
    margin-bottom: 10px;
  }

  .tc1 {
    background-color: #fff;
    padding-bottom: 20px;
    overflow: hidden;
  }

  .m-scroll {
    height: 100%;
    overflow: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
</style>



